h1 {
  font-size: 2em;
}


.navbar, .navbar-end, .navbar-menu, .navbar-start {
  align-items: stretch !important;
  display: flex !important;
}

.navbar-menu {
  flex-grow: 1 !important;
  flex-shrink: 0 !important;
  box-shadow: none !important;
  padding: 0 !important;

}
progress[value] {
  padding-left: 100px;
  width: 1300px;
  height: 20px;
}

progress[value]::-webkit-progress-value::after {
  content: '';
  position: center;
  border-radius: 100%;
  right: 7px;
  top: 7px;
  background-color: white;
}

.btn-xl {
  padding: 5px 90px;
  font-size: 19px;
  border-radius: 5px;
  width:1300px;    
}


.center {
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  height: 50%;
}



.navbar-item {
  display: flex !important;
  padding: 0 0.5rem;
}

.navbar-end {
  
  justify-content: flex-end !important;
  margin-left: auto !important;
}

.features {
  padding-bottom: 3em;
}

body {
  visibility: visible !important;
}


.auth form {
  margin-top: 1em;
  max-width: 50%;
}

.buttons {
  
  margin-left: 1em;
  
}